@keyframes attention {
  0%,
  100% {
    transform: translateX(0%);
    transform-origin: 50% 50%; }
  15% {
    transform: translateX(-30px) rotate(-6deg); }
  30% {
    transform: translateX(15px) rotate(6deg); }
  45% {
    transform: translateX(-15px) rotate(-3.6deg); }
  60% {
    transform: translateX(9px) rotate(2.4deg); }
  75% {
    transform: translateX(-6px) rotate(-1.2deg); } }

* {
  box-sizing: border-box; }

html, body {
  width: 100%;
  height: 100%; }

body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 300; }

h1, h2, h3, h4, p {
  padding: 0;
  margin: 0; }

h1 {
  font-size: 2.5rem;
  font-weight: 600;
  line-height: 1.4; }

h2 {
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.4; }

h3 {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.4; }

h4 {
  font-size: 1.25rem;
  font-weight: 300;
  line-height: 1.4; }

.bbtn {
  display: inline-block;
  position: relative;
  text-decoration: none;
  margin: 0;
  word-spacing: normal;
  text-shadow: none;
  outline: none;
  transition: ease-out .2s;
  text-align: center;
  line-height: 1; }
  .bbtn:active {
    outline: none; }
  .bbtn:focus {
    outline: none; }
  .bbtn:hover {
    cursor: pointer;
    box-shadow: 0 0 0.4rem rgba(14, 14, 14, 0.5); }
  .bbtn-fw {
    width: 100%; }
  .bbtn-blue {
    background-color: #4260FF;
    border: 1px solid #4260FF;
    color: #ffffff; }
    .bbtn-blue:hover {
      background-color: #ffffff;
      color: #191919; }
  .bbtn-1x {
    padding: 1rem;
    font-size: 1.375rem;
    font-weight: 400; }

#main {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.main__container {
  max-width: 900px;
  width: 100%;
  margin-left: auto;
  margin-right: auto; }

.main__block {
  width: 100%;
  height: 100%;
  background: rgba(63, 63, 63, 0.6);
  border-radius: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 2rem; }
  .main__block_empty {
    width: 100%;
    height: 100%;
    font-size: 1.5rem;
    line-height: 1.4;
    text-align: center;
    color: #ffffff;
    padding-top: 5rem;
    position: relative; }
    .main__block_empty:before {
      content: '!';
      position: absolute;
      width: 4rem;
      height: 4rem;
      font-size: 3rem;
      font-weight: 600;
      color: #ff0000;
      top: 0;
      left: 50%;
      margin-left: -2rem;
      animation: attention 0.8s infinite both; }
  .main__block_items {
    width: 65%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap; }
  .main__block_item {
    width: 18%;
    margin: 1%;
    overflow: hidden;
    border-radius: .4rem; }
    .main__block_item img {
      width: 100%;
      height: auto;
      display: block;
      box-sizing: border-box; }
  .main__block_param {
    width: 32.5%; }
    .main__block_param-rover {
      margin-top: .5rem;
      position: relative; }
      .main__block_param-rover label {
        font-size: .625rem;
        color: #9f9f9f;
        font-weight: 300;
        line-height: 1;
        position: absolute;
        top: .4rem;
        left: 1rem; }
      .main__block_param-rover select {
        outline: none;
        width: 100%;
        background-color: #ffffff;
        font-size: 1.375rem;
        line-height: 1;
        font-weight: 300;
        color: #585555;
        padding-left: .8rem;
        padding-top: 1rem;
        padding-bottom: .1rem;
        border: none; }
    .main__block_param-camera {
      margin-top: 1.5rem;
      position: relative; }
      .main__block_param-camera label {
        font-size: .625rem;
        color: #9f9f9f;
        font-weight: 300;
        line-height: 1;
        position: absolute;
        top: .4rem;
        left: 1rem; }
      .main__block_param-camera select {
        outline: none;
        width: 100%;
        background-color: #ffffff;
        font-size: 1.375rem;
        line-height: 1;
        font-weight: 300;
        color: #585555;
        padding-left: .8rem;
        padding-top: 1rem;
        padding-bottom: .1rem;
        border: none; }
    .main__block_param-day {
      margin-top: 1.5rem;
      position: relative; }
      .main__block_param-day label {
        font-size: .625rem;
        color: #9f9f9f;
        font-weight: 300;
        line-height: 1;
        position: absolute;
        top: .4rem;
        left: 1rem; }
      .main__block_param-day input {
        width: 100%;
        background-color: #ffffff;
        font-size: 1.375rem;
        line-height: 1;
        font-weight: 300;
        color: #585555;
        padding-left: 1rem;
        padding-top: 1rem;
        padding-bottom: .1rem;
        border: none; }
    .main__block_param-btn {
      margin-top: 1.5rem; }

@media (max-width: 1000px) {
  .main__container {
    max-width: 90%; } }

@media (max-width: 768px) {
  .main__container {
    max-width: 80%; }
  .main__block {
    flex-direction: column; }
    .main__block_items {
      width: 100%; }
    .main__block_param {
      width: 96%;
      margin-left: auto;
      margin-right: auto;
      margin-top: 1rem;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      flex-wrap: wrap; }
      .main__block_param-rover {
        width: 48%;
        margin-top: 0;
        margin-bottom: 1rem; }
      .main__block_param-camera {
        width: 48%;
        margin-top: 0;
        margin-bottom: 1rem; }
      .main__block_param-day {
        width: 48%;
        margin-top: 0; }
      .main__block_param-btn {
        width: 48%;
        margin-top: 0; }
        .main__block_param-btn button {
          padding: .75rem; } }

@media (max-width: 400px) {
  html {
    font-size: 12px; }
  .main__container {
    max-width: 100%; }
  .main__block {
    border-radius: 0; }
    .main__block_items {
      order: 2; }
    .main__block_item {
      width: 30%; }
    .main__block_param {
      width: 80%;
      margin-top: 0;
      margin-bottom: 2rem;
      order: 1;
      flex-direction: column; }
      .main__block_param-rover {
        width: 100%; }
      .main__block_param-camera {
        width: 100%; }
      .main__block_param-day {
        width: 100%;
        margin-bottom: 1rem; }
      .main__block_param-btn {
        width: 100%; } }
